/* Home */
#root {
  font-family: "Audiowide", cursive;
}
.btn {
  width: 100px;
  border: 1px solid black;
}
.purple:hover {
  color: white;
}
.btn:focus {
  outline: none;
  box-shadow: none;
}

.purple {
  background-color: purple;
  color: white;
}

.white {
  color: white;
}

.red {
  color: rgb(255, 0, 64);
}

.btn-warning {
  color: white !important;
}

.btn-play {
  width: 30%;
}

.btn-default {
  background-color: whitesmoke;
}

.coral:disabled {
  opacity: 1;
  background-color: coral;
  color: white;
}

.aqua:disabled {
  opacity: 1;
  background-color: rgba(0, 255, 255, 0.555);
  color: white;
}

.lime:disabled {
  opacity: 1;
  background-color: limegreen;
  color: white;
}

.bg-main {
  background-color: slategray;
}

.badge-1 {
  background-color: rgb(127, 80, 255);
  font-size: 13px;
  padding: 8px;
  border-radius: 5px;
  color: whitesmoke;
}

.badge-2 {
  background-color: rgb(18, 180, 32);
  font-size: 13px;
  padding: 8px;
  border-radius: 5px;
  color: whitesmoke;
}

.bg-loading {
  background: url("./2.gif");
  background-repeat: no-repeat;
}

.loading {
  width: 65px;
  height: 65px;
}

.choice:hover {
  background-color: rgb(255, 125, 78, 0.2);
}

.question-holder {
  border: solid black 2px;
  background-color: whitesmoke;
  height: 50vh;
}

.next:hover {
  background-color: tomato;
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
  background-color: tomato;
}

.progress-bar {
  -webkit-transition: none !important;
  transition: none !important;
}

.btn-group .btn {
  border-top-left-radius: 5px !important;
}

.container {
  width: 75vw !important;
}

.question {
  height: 33vh;
}

.random-bg {
  height: 80vh;
  background-image: url("https://picsum.photos/1920/1080");
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.score-holder {
  top: 40%;
  left: 30%;
  padding: 5px;
  font-size: 25px;
  color: whitesmoke;
  border-radius: 5px;
  background-color: rgb(0, 0, 0, 0.8);
}

@media only screen and (max-width: 1000px) {
  .btn-play {
    width: 100%;
  }

  .custom-control-label {
    top: 40%;
  }

  .btn-group .btn {
    border-radius: 5px !important;
  }

  .question-holder {
    height: 70vh;
  }

  .margin-adjust {
    margin-left: 0 !important;
  }

  .badge-1 {
    font-size: 10px;
    margin-right: 2px;
    padding: 6px;
  }

  .badge-2 {
    font-size: 10px;
    padding: 6px;
  }

  .footer {
    margin-top: 65% !important;
  }

  .question-font {
    font-size: 20px;
  }

  .custom-control-input {
    font-size: 8px !important;
  }

  .navbar-item {
    padding-left: 0 !important;
  }
  .navbar-container {
    margin-top: 10%;
  }

  .random-bg {
    height: 80vh;
    background-image: url("https://picsum.photos/300/600");
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  .score-holder {
    top: 20%;
    left: 10%;
    padding: 5px;
    font-size: 12px;
    color: whitesmoke;
    border-radius: 5px;
    background-color: rgb(0, 0, 0, 0.8);
  }

  .progress {
    margin-bottom: 5px;
  }
}
